.forum-discussion-card {
    display: flex;
    flex-direction: column;
    gap: 10px;
    border: 1px solid var(--gray-300);
    border-left: 4px solid var(--gray-300);
    padding: 10px;

    .forum-discussion-card-row1 {
        display: flex;
        gap: 10px;

        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }

        .forum-discussion-info {
            display: flex;
            flex-direction: column;

            a {
                color: var(--gray-500);

                h6 {
                    font-size: 16px;
                    font-weight: 500;
                }
            }

            a:hover {
                text-decoration: underline;
                cursor: pointer;
            }

            small {
                font-size: 13px;
                font-style: italic;
            }
        }
    }

    .forum-discussion-card-row2 {

        mask-image: linear-gradient(180deg, #555 60%, transparent);
        min-height: 38px;
        max-height: 60px;
        overflow: hidden;

        p {
            margin: 0;
        }
    }

    .forum-discussion-card-row3 {
        .view-reply-like-counter {
            display: flex;
            gap: 20px;
            font-size: 13px;
            font-style: italic;

            .like-counter,
            .reply-counter,
            .view-counter {
                display: flex;
                align-items: center;
                gap: 5px;
            }

            .like-counter {
                .like-msg-icon {
                    display: flex;
                    width: 17px;
                    height: 16px;
                    fill: var(--black-102);
                }
            }

            .reply-counter {
                .reply-msg-icon {
                    display: flex;
                    width: 16px;
                    height: 16px;
                }
            }

            .view-counter {
                .view-msg-icon {
                    display: flex;
                    width: 17px;
                    height: 12px;
                }
            }
        }
    }
}